  public getSingleProduct(id: number): Observable<any> {
    let temp: any;
    this.products.forEach(element => {
      if (element.id == id) {
            temp = element;
      }
    });
    const loading = false;
    this.loadingService.progressEnable.next(loading);
    return new Observable((observer) => observer.next(temp));
  }
